<template>
  <div
    class="music-container"
    :style="{background: 'center url('+ backgroundPic +') no-repeat' }">
    <div class="player">
      <div :class="isTimerPlaying ? 'player-cover cover-scale-up' : 'player-cover'">
        <transition-group :name="transitionName">
          <template v-for="(track, index) in tracks">
            <div
              class="player-cove-item"
              :key="index"
              v-if="index === currentTrackIndex"
              :style="{ backgroundImage: `url(${track.cover})` }">
            </div>
          </template>
        </transition-group>
      </div>
      <div class="player-down">
        <div class="info">
          <p class="name">{{ currentTrack.name }}</p>
          <p class="singer">{{ currentTrack.singer }}</p>
        </div>
        <div class="progress-bar" ref="progress" @click="changeProgress">
          <div class="progress-duration" :style="{ width : currentBarWidth }">
          </div>
        </div>
        <div class="progress-info">
          <p class="progress-played">{{ currentTime }}</p>
          <p class="progress-left">{{ duration }}</p>
        </div>
        <div class="control">
          <a class="control-icon" @click="playPre"><i class="fa fa-backward fa-2x"></i></a>
          <a class="control-icon btn-play" @click="play">
            <i :class="isTimerPlaying ? 'fa fa-pause fa-2x' : 'fa fa-play fa-2x'"></i>
          </a>
          <a class="control-icon" @click="playNext"><i class="fa fa-forward fa-2x"></i></a>
        </div>
        <div class="volume">
          <a class="volume-icon"><i class="fa fa-volume-down"></i></a>
          <div class="volume-control" ref="volume" @click="chooseVolume">
            <div class="volume-duration" :style="{ width : currentVolumeWidth }"></div>
          </div>
          <a class="volume-icon"><i class="fa fa-volume-up"></i></a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import { PicUrl, Tracks } from '../congfig'
  export default{
    name:'music',
    components:{
    },
    data(){
      return {
        backgroundPic: PicUrl.music,
        currentTrackIndex: 0,
        currentBarWidth: 0,
        duration: null,
        currentVolumeWidth: '30%',
        currentTime: '',
        isTimerPlaying: false,
        currentTrack: null,
        transitionName: null,
        tracks: Tracks,
      }
    },
    created() {
      let $vue = this;
      this.currentTrack = this.tracks[0];
      this.audio = new Audio();
      this.audio.src = this.currentTrack.url;
      this.audio.ontimeupdate = function() {
        $vue.generateTime();
      };
      this.audio.onloadedmetadata = function() {
        $vue.generateTime();
      };
      this.audio.onended = function() {
        $vue.playNext();
        this.isTimerPlaying = true;
      };
      // this is optional (for preload covers)
      for (let index = 0; index < this.tracks.length; index++) {
        const element = this.tracks[index];
        let link = document.createElement('link');
        link.rel = "prefetch";
        link.href = element.cover;
        link.as = "image"
        document.head.appendChild(link)
      }
    },

    mounted(){},
    methods:{
      play() {
        if (this.audio.paused) {
          this.audio.play();
          this.isTimerPlaying = true;
        } else {
          this.audio.pause();
          this.isTimerPlaying = false;
        }
      },
      generateTime() {
        let width = this.audio.currentTime / this.audio.duration * 100;
        this.currentBarWidth = width + "%";
        let durmin = Math.floor(this.audio.duration / 60);
        let dursec = Math.floor(this.audio.duration - durmin * 60);
        let curmin = Math.floor(this.audio.currentTime / 60);
        let cursec = Math.floor(this.audio.currentTime - curmin * 60);
        if (durmin < 10) {
          durmin = "0" + durmin;
        }
        if (dursec < 10) {
          dursec = "0" + dursec;
        }
        if (curmin < 10) {
          curmin = "0" + curmin;
        }
        if (cursec < 10) {
          cursec = "0" + cursec;
        }
        this.duration = durmin + ":" + dursec;
        this.currentTime = curmin + ":" + cursec;
      },
      changeProgress(e) {
        this.isTimerPlaying = true;
        this.audio.pause();
        this.updateBar(e.offsetX);
      },
      updateBar(x) {
        let progress = this.$refs.progress;
        let maxduration = this.audio.duration;
        let position = x - progress.offsetLeft;
        let percentage = (100 * position) / progress.offsetWidth;
        if (percentage > 100) {
          percentage = 100;
        }
        if (percentage < 0) {
          percentage = 0;
        }
        this.currentBarWidth = percentage + "%";
        this.audio.currentTime = (maxduration * percentage) / 100;
        this.audio.play();
      },
      playNext() {
        this.transitionName = "scale-out";
        if (this.currentTrackIndex < this.tracks.length - 1) {
          this.currentTrackIndex ++ ;
        } else {
          this.currentTrackIndex = 0;
        }
        this.currentTrack = this.tracks[this.currentTrackIndex];
        this.resetPlayer();
      },
      playPre() {
        this.transitionName = "scale-in";
        if (this.currentTrackIndex > 0) {
          this.currentTrackIndex--;
        } else {
          this.currentTrackIndex = this.tracks.length - 1;
        }
        this.currentTrack = this.tracks[this.currentTrackIndex];
        this.resetPlayer();
      },
      resetPlayer() {
        this.currentBarWidth = 0;
        this.audio.currentTime = 0;
        this.audio.src = this.currentTrack.url;
        setTimeout(() => {
          if(this.isTimerPlaying) {
            this.audio.play();
          } else {
            this.audio.pause();
          }
        }, 300);
      },
      chooseVolume(e) {
        let volume = this.$refs.volume;
        let position = e.offsetX - volume.offsetLeft;
        let percentage = 100 * position / volume.offsetWidth;
        if (percentage > 100) {
          percentage = 100;
        }
        if (percentage < 0) {
          percentage = 0;
        }
        this.currentVolumeWidth = percentage + "%";
        this.audio.volume = percentage / 100;
      }
    }
  }
</script>

<style lang="scss">
  @import "../assets/mixin";
  @import "../assets/config.scss";
  .music-container {
    @include wh();
    background-size: cover;
    position: relative;
    .player {
      @include positionTL(absolute, 15vh, 50%);
      height: 65vh;
      width: 17vw;
      transform: translate(-50%);
      background: #eef3f7;
      box-shadow: 0 15px 35px -5px rgba(50, 88, 130, 0.32);
      border-radius: 15px;
      padding: 30px;
      .player-cover {
        @include positionTL(absolute, 30px, 50%);
        transform: translate(-50%);
        @include wh(12vw, 12vw);
        overflow: hidden;
        border-radius: 10px;
        transition: all .5s;
        .player-cove-item {
          @include wh(12vw, 12vw);
          border-radius: 10px;
          background: no-repeat center;
          background-size: cover;
        }
      }
      .cover-scale-up {
        transform: translate(-50%) scale(1.3);
        /*transform-origin: 0 0;*/
      }
      .player-down {
        @include flex();
        align-items: flex-start;
        @include positionTL(absolute, 250px,30px);
        width: 17vw;
        cursor: pointer;
        .info {
          font-size: 15px;
          .name {
            font-weight: bold;
            color: #000;
          }
          .singer {
            font-weight: bold;
            color: #dd6161;
          }
        }
        .progress-bar {
          height: 6px;
          width: 100%;
          margin-top: 10px;
          cursor: pointer;
          background-color: #d0d8e6;
          display: inline-block;
          border-radius: 10px;
          .progress-duration {
            position: relative;
            height: 6px;
            width: 50%;
            border-radius: 10px;
            background-color: blue;
            &:before {
              content: " ";
              position: absolute;
              top: -3px;
              right: -6px;
              background-color: #000;
              @include wh(12px, 12px);
              border-radius: 6px;
            }
          }
        }
        .progress-info {
          width: 100%;
          margin-top: 10px;
          @include flex();
          flex-direction: row;
          justify-content: space-between;
        }
        .control {
          width: 100%;
          margin-top: 20px;
          @include flex();
          flex-direction: row;
          justify-content: center;
          .btn-play {
            margin: 0 30px;
          }
          .control-icon {
            @include wh(30px, 30px);
            background: center no-repeat;
            background-size: cover;
            @include flex_center();
          }
        }
        .volume {
          width: 100%;
          margin-top: 30px;
          @include flex();
          flex-direction: row;
          justify-content: space-around;
          .volume-icon {
            @include flex_center();
          }
          .volume-control {
            height: 6px;
            width: 100%;
            background-color: #d0d8e6;
            display: inline-block;
            border-radius: 10px;
            margin: 0 10px;
            .volume-duration {
              position: relative;
              height: 6px;
              width: 50%;
              border-radius: 10px;
              background-color: #dd6161;
              &:before {
                content: " ";
                position: absolute;
                top: -3px;
                right: -6px;
                background-color: #8cc5ff;
                @include wh(12px, 12px);
                border-radius: 6px;
              }
            }
          }
        }
      }
    }
  }

  .scale-out-enter-active {
    transition: all .35s ease-in-out;
  }
  .scale-out-leave-active {
    transition: all .35s ease-in-out;
  }
  .scale-out-enter {
    transform: scale(.55);
    pointer-events: none;
    opacity: 0;
  }
  .scale-out-leave-to {
    transform: scale(1.2);
    pointer-events: none;
    opacity: 0;
  }


  .scale-in-enter-active {
    transition: all .35s ease-in-out;
  }
  .scale-in-leave-active {
    transition: all .35s ease-in-out;
  }
  .scale-in-enter {
    transform: scale(1.2);
    pointer-events: none;
    opacity: 0;
  }
  .scale-in-leave-to {
    transform: scale(.55);
    pointer-events: none;
    opacity: 0;
  }

</style>
